@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,400;1,500&family=Roboto:wght@100;300;400;500;700;900&display=swap");
@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  // font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  //   "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  //   sans-serif;
  font-family: "Poppins", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-decoration: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.icon--header {
  @apply flex flex-col items-center mx-5 text-[13px] text-[#82869e] font-medium;
}

.clip-path-left0 {
  clip-path: polygon(0 0, 0 0, 0 100%, 0% 100%);
  transition: all 0.25s ease;
}

.clip-path-left100 {
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  transition: all 0.25s ease;
}
